.nav {
  grid-area: n;
  background-color: #1b1b1a;
  padding: 20px;
  border-radius: 10px;
  transition: 0.5s;
}

.item a {
  color: #eeeeee;
  text-decoration: none;
  font-size: 20px;
  margin: 5px;
  font-size: 20px;
  display: block;
  text-align: center;
  position: relative;
  transition: 0.3s;
  z-index: 1;
  overflow: hidden;
}

.item a.activeLink {
  color: #f04747;
  font-style: bold;
}

.item a::before,
.item a::after {
  position: absolute;
  height: 100%;
  width: 100%;
  content: "";
  background: #2c2f33;
  top: 0;
  left: -90%;
  opacity: 0.5;
  transition: 0.03s;
  z-index: -1;
}

.item a::after {
  opacity: 0.7;
  transition-delay: 0.08s;
  left: -92%;
}

.item a:hover::before,
.item a:hover::after {
  left: 0;
}
