.dialogs {
  display: grid;
  grid-template-columns: 2fr 10fr;
}

.dialogItem a {
  color: #eeeeee;
  text-decoration: none;
}

.dialogItem a.activeLink {
  color: #d65a31;
  text-decoration: none;
}

.dialogsItems {
  padding: 10px;
  color: white;
}

.dialogsItems .active {
  color: gold;
}

.messages {
  padding: 10px;
}

.messages .message {
  color: white;
}
