.postsBlock {
  margin-top: 50px;
  padding: 10px;
  color: #eeeeee;
}

.posts {
  margin-top: 10px;
}

.butt {
  border: 2px solid #1b1b1a;
  font-size: 18px;
  color: #1b1b1a;
  display: block;
  text-align: center;
  position: relative;
  transition: 0.5s;
  z-index: 1;
  overflow: hidden;
}

