.Navbar_item__yuwH8 a::before,
.Navbar_item__yuwH8 a::after {
  position: absolute;
  height: 100%;
  width: 100%;
  content: "";
  background: #2c2f33;
  top: 0;
  left: -90%;
  opacity: 0.5;
  transition: 0.03s;
  z-index: -1;
}

.Navbar_item__yuwH8 a::after {
  opacity: 0.7;
  transition-delay: 0.08s;
  left: -92%;
}

.Navbar_item__yuwH8 a:hover::before,
.Navbar_item__yuwH8 a:hover::after {
  left: 0;
}

