.paginationBttns {
  width: 80%;
  height: 40px;
  list-style: none;
  display: flex;
  justify-content: center;
}

.paginationBttns a {
  margin: 6px;
  border-radius: 1px;
  border: 0.1px solid #2c2f33;
  padding: 1px 8px;
  cursor: pointer;
}

.paginationBttns a:hover {
  background-color: #2c2f33;
  transition: 0.1s;
}
