.app-wrapper {
  margin: 0 auto;
  display: grid;
  width: 1200px;
  grid-template-areas:
    "h h"
    "n c";

  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  grid-gap: 10px;
  background-color: #2c2f33;
}

.app-wrapper-content {
  grid-area: c;
  background-color: #1b1b1a;
  border-radius: 10px;
  padding: 20px;
}

